/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react'
import { Row, Col } from 'reactstrap'
import Logo1 from '../../../assets/img/logo/logo-mast-removebg-preview.png'
import Logo2 from '../../../assets/img/logo/logo-mast-removebg-preview.png'
import './typewriter.css'
import { Plans } from './Plans'
import { Features } from './Features'
import { DownloadOptions } from './DownloadOptions'
import { WhyUs } from './WhyUs'

export const AlwaysOn = (props) => {
  const theme = localStorage.getItem('theme')
  // const deviceWidth = useWindowSize().width
  const deviceWidth = window.innerWidth

  const { plan, setplan, why, setwhy, feature, setfeature, download } = props
  return (
    <div
      className="why-menu-option animate"
      id="AlwaysOn"
      style={{
        textAlign: 'center',
        height: 'fit-content'
      }}
    >
      {deviceWidth > 500 ? (
        <Row style={{ height: '420px' }}>
          <Col
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: '#fffbef'
            }}
          >
            <p style={{ color: 'black' }}>
              Last action before you bid good-bye
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              You might not have control over your last moment,
              <br />
              But now you can control your last acton in this world
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              Leave behind a peaceful world, free from family disputes
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              That's what we say, Rest In Peace
            </p>
          </Col>
          <Col
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: '#ffebd2'
            }}
          >
            <Row style={{ justifyContent: 'center', alignItems: 'center' }}>
              <img
                alt="logo"
                style={{ marginRight: '5px', height: '50px' }}
                className="brand-logo"
                height="25"
                src={theme === 'dark' ? Logo2 : Logo1}
              />
              <h2
                className="brand-text mb-0"
                style={{
                  fontWeight: '600',
                  fontSize: '50px',
                  color: 'black'
                }}
              >
                Last Arzi
              </h2>
            </Row>
            <Row>
              <Col
                style={{
                  margin: '10px auto'
                }}
                className="typewriter pr-50"
              >
                What{' '}
                <span
                  style={{
                    fontWeight: 'bold',
                    color: 'var(--warning)'
                  }}
                >
                  WILL
                </span>{' '}
                you leave behind
                <span style={{ color: 'var(--warning)' }}> ...</span>
              </Col>
            </Row>
          </Col>
        </Row>
      ) : (
        <Col style={{ height: '600px' }}>
          <Row
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: '#ffebd2',
              height: '40%',
              padding: '50px 0 0'
            }}
          >
            <Col style={{ justifyContent: 'center', alignItems: 'center' }}>
              <img
                alt="logo"
                style={{ marginRight: '5px', height: '50px' }}
                className="brand-logo"
                height="25"
                src={theme === 'dark' ? Logo2 : Logo1}
              />
              <h2
                className="brand-text mb-0"
                style={{
                  fontWeight: '600',
                  fontSize: '50px',
                  color: 'black'
                }}
              >
                Last Arzi
              </h2>
            </Col>
            <Col>
              <Row
                style={{
                  marginTop: '10px',
                  justifyContent: 'center'
                }}
              >
                What{' '}
                <span
                  style={{
                    fontWeight: 'bold',
                    color: 'var(--warning)'
                  }}
                >
                  WILL
                </span>{' '}
                you leave behind
                <span style={{ color: 'var(--warning)' }}> ...</span>
              </Row>
            </Col>
          </Row>
          <Row
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
              height: '60%',
              backgroundColor: '#fffbef',
              padding: '0px 10px'
            }}
          >
            <p style={{ color: 'black' }}>
              Last action before you bid good-bye
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              You might not have control over your last moment,
              <br />
              But now you can control your last acton in this world
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              Leave behind a peaceful world, free from family disputes
            </p>
            <p className="mt-2" style={{ color: 'black' }}>
              That's what we say, Rest In Peace
            </p>
          </Row>
        </Col>
      )}
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          width: '100%',
          margin: '30px 0'
        }}
      >
        <div
          block="true"
          style={{
            width: '100%',
            color: feature ? 'orange' : 'grey',
            fontWeight: feature ? 'bold' : 'medium',
            fontSize: '18px',
            padding: '5px 0',
            cursor: 'pointer',
            borderBottom: feature ? '2px solid orange' : 'none'
          }}
          className="btn-block"
          onClick={() => {
            setplan(false)
            setfeature(true)
            setwhy(false)
          }}
        >
          Feature
        </div>
        <div
          block="true"
          style={{
            width: '100%',
            color: why ? 'orange' : 'grey',
            fontWeight: why ? 'bold' : 'medium',
            fontSize: '18px',
            padding: '5px 0',
            cursor: 'pointer',
            borderBottom: why ? '2px solid orange' : 'none'
          }}
          className="btn-block"
          onClick={() => {
            setplan(false)
            setfeature(false)
            setwhy(true)
          }}
        >
          Why Last Arzi
        </div>

        <div
          block="true"
          style={{
            width: '100%',
            color: plan ? 'orange' : 'grey',
            fontWeight: plan ? 'bold' : 'medium',
            fontSize: '18px',
            cursor: 'pointer',
            padding: '5px 0',
            borderBottom: plan ? '2px solid orange' : 'none'
          }}
          className="btn-block"
          onClick={() => {
            setplan(true)
            setfeature(false)
            setwhy(false)
          }}
        >
          Plans
        </div>
      </div>
      <div>
        {' '}
        {why && <WhyUs />}
        {feature && <Features />}
        {plan && <Plans />}
        {download && <DownloadOptions />}
      </div>
    </div>
  )
}
