import React from 'react'
import themeConfig from '../../../configs/themeConfig'
import './typewriter.css'

const Dark = themeConfig.theme === 'dark'

export const WhyUs = () => {
  return (
    <div
      className="why-menu-option"
      id="whyUs"
      style={{
        backgroundColor: Dark ? '#21212a' : 'white',
        paddingTop: '10px'
      }}
    ></div>
  )
}
