import React, { useState } from 'react'
import {
  Card,
  CardHeader,
  CardBody,
  Form,
  Input,
  FormGroup,
  Label,
  Button
} from 'reactstrap'
import csImg from '../../../assets/img/rocket.png'
import '../../../assets/scss/pages/coming-soon.scss'
import Countdown from 'react-countdown-now'

const ComingSoon = () => {
  const renderTimer = ({ days, hours, minutes, seconds }) => {
    return (
      <React.Fragment>
        <div className="clockCard px-1">
          <p>{days}</p>
          <p className="bg-amber clockFormat lead px-1 black"> Days </p>
        </div>
        <div className="clockCard px-1">
          <p>{hours}</p>
          <p className="bg-amber clockFormat lead px-1 black"> Hours </p>
        </div>
        <div className="clockCard px-1">
          <p>{minutes}</p>
          <p className="bg-amber clockFormat lead px-1 black"> Minutes </p>
        </div>
        <div className="clockCard px-1">
          <p>{seconds}</p>
          <p className="bg-amber clockFormat lead px-1 black"> Seconds </p>
        </div>
      </React.Fragment>
    )
  }
  const [email, setEmail] = useState('')
  const handlingemail = (e) => {
    e.preventDefault()
    setEmail(e.target.value)
  }
  const date = new Date('2021-07-15')
  return (
    <Card className="mb-0">
      <CardHeader className="justify-content-center">
        <h3>Launching on July 15th</h3>
      </CardHeader>
      <CardBody className="text-center">
        <img
          src={csImg}
          alt="csImg"
          className="img-fluid width-150"
          style={{ height: '100px' }}
        />
        <div className="text-center getting-started pt-1 d-flex justify-content-center flex-wrap">
          <Countdown date={date.getTime()} renderer={renderTimer} />
        </div>
        <div className="divider">
          <div className="divider-text">Subscribe</div>
        </div>
        <p className="text-left mb-2">
          Please subscribe to get notified when our app is live.
        </p>
        <Form>
          <FormGroup className="form-label-group">
            <Input placeholder="Email" onChange={handlingemail} />
            <Label>Email</Label>
          </FormGroup>
        </Form>
        <Button.Ripple
          block
          color={email ? 'warning' : 'primary'}
          className="btn-block"
        >
          Subscribe
        </Button.Ripple>
      </CardBody>
    </Card>
  )
}
export default ComingSoon
